.footer {
  display: flex;
  flex-direction: column;;
  gap: 32px;

  width: 100%;
  padding-block: 32px;
  background-color: $c-white;
  box-shadow: 0px -1px 0px 0px $c-elements-light-grey;


  @include paddingInline;

  &__logo {
    display: flex;
  }

  &__scroll-up {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;

    @include onTablet {
      justify-content: flex-end;
    }
  }

  &__items {
    display: flex;
    gap: 16px;
    flex-direction: column;
    justify-content: space-between;

    @include onTablet {
      flex-direction: row;
    }
  }

  &__button-label {
    color: $c-secondary-grey;
  }

  @include onTablet {
    flex-direction: row;
    align-items: center;
  }

  & > * {
    flex-grow: 1;
  }
}
